import { createRouter, createWebHistory } from 'vue-router'
import Home from "@/pages/Home.vue";
import LoginJoin from "@/pages/LoginJoin.vue";
const routes = [
  {
    path: '/',
    redirect: '/LoginJoin',
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
  },
  {
    path: "/LoginJoin",
    name: "LoginJoin",
    component: LoginJoin,
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
